.c-features {
  &__bigWrap {
    flex: 0 0 calc(100% - 24.5% * 2 - 10px * 2);
    max-width: calc(100% - 24.5% * 2 - 10px * 2);
    @include media($tablet-portrait) {
      flex: 0 0 100%;
      max-width: 100%;
      margin-bottom: 10px;
    }
  }
  &__bigItem {
    width: 100%;
    height: 100%;
    transition: .3s all;
    @include bgSetting("https://www.nownews.com/wp-content/uploads/2019/07/1562745415-9cc177bb196370cad5a4af16505511e3.png");
    @include media($tablet-portrait) {
      height: auto;
      padding-bottom: 60%;
    }
    &::before {
      @extend %full;
      content: '';
      position: absolute;
      background: linear-gradient(180deg,rgba(0,0,0,0),rgba(0,0,0,0) 40%,rgba(0,0,0,0.6));
    }
    &:hover {
      transform: scale(1.015);
    }
    &__text {
      left: 16px;
      bottom: 18px;
      &__title {
        font-size: 32px;
        letter-spacing: 3.5px;
        font-weight: bolder;
        color: white;
        line-height: 1.3;
        @include media($desktop) {
          font-size: 30px;
        }
        @include media($tablet-landscape) {
          font-size: 20px;
        }
      }
    }
  }
  &__smallWrap {
    flex: 0 0 24.5%;
    max-width: 24.5%;
    @include media($tablet-portrait) {
      overflow-x: auto;
      overflow-y: hidden;
      white-space: nowrap;
      -webkit-overflow-scrolling: touch;
      flex: 0 0 100%;
      max-width: 100%;
    }
    &:last-child {
      @include media($tablet-portrait) {
        display: none;
      }
    }
  }
  &__smallItem {
    padding-bottom: 79.7%;
    transition: .3s all;
    @include bgSetting("https://www.nownews.com/wp-content/uploads/2019/07/1562745415-9cc177bb196370cad5a4af16505511e3.png");
    @include media($tablet-portrait) {
      width: 300px;
      flex: 0 0 300px;
      padding-bottom: 45%;
      margin-right: 10px;
    }
    &:first-child {
      margin-bottom: .625rem;
      @include media($tablet-portrait) {
        margin-bottom: 0;
      }
    }
    &:last-child {
      @include bgSetting("https://www.nownews.com/wp-content/uploads/2019/07/1562745415-9cc177bb196370cad5a4af16505511e3.png");
    }
    &:hover {
      transform: scale(1.015);
    }
  }
}