.c-hidoPosts {
  &__title {
    text-align: center;
    font-size: 24px;
    line-height: 1;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__post {
    transition: .3s all;
    &:hover {
      box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.2);
      transform: translateY(-3px);
    }
    &__img {
      @include bgSetting("https://www.nownews.com/wp-content/uploads/2019/07/1562745415-9cc177bb196370cad5a4af16505511e3.png");
    }
    &__info {
      font-size: 14px;
      line-height: 1.4;
      font-weight: 700;
      color: map-get($colors, black);
    }
  }
}