body {
  font-size: 16px;
  font-family: "PingFangTC-Regular","Microsoft JhengHei";
  color: #4a4a4a;
}

.wrap {
  max-width: 1400px;
}

.c-features {
  @include media($tablet-landscape) {
    margin-bottom: 20px;
  }
  .tags {
    background-color: rgba(0,0,0,.7);
    margin-bottom: .625rem;
  }
}

.c-subFeatures {
  max-width: 1240px;
  padding: 20px;
  background-color: map-get($colors, pink);
  margin-bottom: 80px;
  @include media($tablet-landscape) {
    padding: 12px;
    margin-bottom: 50px;
  }
}

.p-container {
  &__sidebar {
    flex: 0 0 25%;
    max-width: 25%;
    @include media($tablet-landscape) {
      flex: 0 0 100%;
      max-width: 100%;
      width: 100%;
    }
  }
  &__main {
    flex: 0 0 50%;
    max-width: 50%;
    @include media($tablet-landscape) {
      flex: 0 0 100%;
      max-width: 100%;
    }
    &__item {
      flex: 0 0 calc( 50% - 16px * 2 );
      max-width: calc( 50% - 16px * 2 );
      margin-bottom: 40px;
      @include media($desktop) {
        flex: 0 0 calc( 50% - 4px * 2 );
        max-width: calc( 50% - 4px * 2 );
      }
      @include media($tablet-landscape) {
        flex: 0 0 calc( 50% - 8px * 2 );
        max-width: calc( 50% - 8px * 2 );
        margin-bottom: 15px;
      }
      @include media($tablet-portrait) {
        flex: 0 0 calc( 100% );
        max-width: calc( 100% );
        margin-bottom: 40px;
      }
    }

    .c-title {
      flex: 0 0 calc( 100% - 16px * 2);
      max-width: calc( 100% - 16px * 2);
      @include media($tablet-portrait) {
        flex: 0 0 calc( 100% );
        max-width: calc( 100% );
      }
    }

    .nowgame__news {
      flex: 0 0 calc( 100% - 16px * 2);
      max-width: calc( 100% - 16px * 2);
      @include media($tablet-landscape) {
        flex: 0 0 calc(100% - 16px );
        max-width: calc(100% - 16px );
      }
      @include media($tablet-portrait) {
        flex: 0 0 calc(100%);
        max-width: calc(100%);
      }
      .newsItem__img {
        padding-bottom: 50%;
        @include media($tablet-portrait) {
          padding-bottom: 80%;
        }
      }
    }
  }
}

.readStories {
  max-height: 415px;
  margin-bottom: 85px;
  @include media($tablet-landscape) {
    margin-bottom: 19px;
  }
  @include media($tablet-portrait) {
    max-height: unset;
  }
  .c-singlePost {
    padding-bottom: 29.7%;
    flex: 0 0 calc( 25% - 2px );
    max-width: calc( 25% - 2px );
    @include media($tablet-portrait) {
      padding-bottom: 60.7%;
      flex: 0 0 calc( 100% );
      max-width: calc( 100% );
      margin-bottom: 24px;
    }
  }
}

.c-hidoPosts {
  @include media($tablet-landscape) {
    width: 70%;
    margin: 0 auto;
  }
  @include media($tablet-portrait) {
    width: 100%;
  }
}

.c-hidoPosts__post {
  &__img {
    flex: 0 0 95px;
    padding: 9.29%;
    @include media($tablet-landscape) {
      padding: 5.29%;
    }
    @include media($tablet-portrait) {
      padding: 9.29%;
    }
  }
}

.slide_Posts {
  @include media($tablet-landscape) {
    max-width: 50%;
    margin: 0 auto;
  }
  @include media($tablet-portrait) {
    max-width: 100%;
  }
  .c-singlePost {
    padding-bottom: 40.36%;
    &__text {
      font-size: 18px;
      line-height: 1.4;
      font-weight: 700;
      color: white;
    }
  }
}

.socialButtons {
  .c-socialButton {
    flex: 0 0 calc( 33.33333% - 4px );
    max-width: calc( 33.33333% - 4px );
    height: 100px;
    margin: 0 2px;
    @include media($tablet-landscape) {
      max-width: 150px;
    }
  }
  .facebook {
    background-color: #516EAB;
  }
  .youtube {
    background-color: #E14E42;
  }
  .instagram {
    background: radial-gradient(circle farthest-corner at 32% 106%, rgb(255, 225, 125) 0%, rgb(255, 205, 105) 10%, rgb(250, 145, 55) 28%, rgb(235, 65, 65) 42%, transparent 82%), linear-gradient(135deg, rgb(35, 75, 215) 12%, rgb(195, 60, 190) 58%);
  }
}

.td_block_trending_now {
  padding: 0 .5rem;
}

.td_stretch_content_1200 .td-container, .td_stretch_content_1200.td-footer-instagram-container .td-instagram-user, .td_stretch_content_1200.td-footer-instagram-container .td_block_instagram {
  max-width: 1400px !important;
}