.newsItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: .3s all;
  &:hover {
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.2);
    transform: translateY(-3px);
  }
  &__img {
    width: 100%;
    @include bgSetting("https://www.nownews.com/wp-content/uploads/2019/07/1562745415-9cc177bb196370cad5a4af16505511e3.png");
    padding-bottom: 80%;
  }
  &__text {
    display: flex;
    flex-direction: column;
    padding: 15px 20px 20px;
    height: 140px;
    width: 100%;
    @include media($tablet-landscape) {
      height: 120px;
      padding: 10px;
      width: 100%;
    }
    @include media($tablet-portrait) {
      padding: 15px 20px 20px;
    }
    p {
      font-size: 18px;
      line-height: 1.4;
      font-weight: 700;
      color: map-get($colors, black);
      @include line;
      @include media($tablet-landscape) {
        font-size: 12px;
      }
      @include media($tablet-portrait) {
        font-size: 18px;
      }
    }
    &__states {
      display: flex;
      left: 21px;
      right: 0;
      bottom: 24px;
      @include media($tablet-landscape) {
        bottom: 14px;
      }
      &__tags {
        font-size: 12px;
        line-height: 1.4;
        font-weight: bolder;
        text-transform: uppercase;
        color: map-get($colors, secondary);
      }
      &__times {
        color: map-get($colors, gray);
        time {
          font-size: 16px;
        }
      }
    }
  }
}