.c-title {
  flex: 0 0 100%;
  @include media($tablet-portrait) {
    width: 100%;
  }
  &__text{
    position: relative;
    font-size: 18px;
    line-height: 1;
    font-weight: bold;
    text-transform: uppercase;
    &::after {
      content: "";
      background-color: #FBC82B;
      position: absolute;
      height: 2px;
      bottom: -7px;
      left: 0;
      right: 0;
      margin: auto;
    }
  }
}