.c-singlePost {
  width: 100%;
  position: relative;
  @include bgSetting("https://www.nownews.com/wp-content/uploads/2019/07/1562745415-9cc177bb196370cad5a4af16505511e3.png");
  &::before {
    @extend %full;
    content: '';
    position: absolute;
    background: linear-gradient(180deg,rgba(0,0,0,0),rgba(0,0,0,0) 40%,rgba(0,0,0,0.6));
  }
  &__text {
    left: 16px;
    bottom: 18px;
    @include media($tablet-portrait) {
      left: 0;
      width: 100%;
      padding: 0 16px;
      box-sizing: border-box;
    }
    &__title {
      font-size: 18px;
      letter-spacing: 1.2px;
      font-weight: bolder;
      color: white;
      line-height: 1.3;
      @include media($desktop) {
        font-size: 15px;
      }
      @include media($tablet-landscape) {
        font-size: 12px;
        font-weight: normal;
      }
      @include media($tablet-portrait) {
        overflow : hidden;
        text-overflow : ellipsis;
        white-space : nowrap;
      }
    }
  }
  &__playIcon {
    position: absolute;
    width: 100%;
    height: 100%;
    @extend %full;
    margin: auto;
    &::before, &::after {
      content: '';
      position: absolute;
      @extend %full;
      margin: auto;
    }
    &::before {
      @include circle(50px, rgba(0, 0, 0, .7));
      border: 2px solid white;
    }
    &::after {
      width: 0;
      height: 0;
      left: 4px;
      border-style: solid;
      border-width: 10px 0 10px 20px;
      border-color: transparent transparent transparent #ffffff;
    }
  }
}