.c-subFeatures {
  &__item {
    flex: 0 0 33.33333%;
    max-width: 33.33333%;
    @include media($tablet-portrait) {
      flex: 0 0 100%;
      max-width: 100%;
      width: 100%;
    }
    &__img {
      flex: 0 0 30%;
      max-width: 30%;
      padding-bottom: 25%;
      @include bgSetting("https://www.nownews.com/wp-content/uploads/2019/07/1562745415-9cc177bb196370cad5a4af16505511e3.png")
    }
  }
  p {
    font-size: 14px;
    line-height: 1.4;
    font-weight: 700;
    color: map-get($colors, black);
    margin-left: 1rem;
  }
}